import React, { Component } from 'react';
import fontawesome from '@fortawesome/fontawesome';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import brands from '@fortawesome/fontawesome-free-brands';
import faCheckSquare from '@fortawesome/fontawesome-free-solid/faCheckSquare';
import faSpinner from '@fortawesome/fontawesome-free-solid/faSpinner';
import faCoffee from '@fortawesome/fontawesome-free-solid/faCoffee';
import faEdit from '@fortawesome/fontawesome-free-solid/faEdit';
import fetch from 'isomorphic-fetch';
import { sortBy } from 'lodash';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import logo from '../../logo.svg';
import './index.css';
import { DEFAULT_QUERY, DEFAULT_HPP, PATH_BASE, PATH_SEARCH, PARAM_SEARCH, PARAM_PAGE, PARAM_HPP, } from '../../constants';

fontawesome.library.add(brands, faCheckSquare, faSpinner, faCoffee, faEdit)

const green = 'green';

const SORTS = {
  NONE: list => list,
  TITLE: list => sortBy(list, 'title'),
  AUTHOR: list => sortBy(list, 'author'),
  COMMENTS: list => sortBy(list, 'num_comments').reverse(),
  POINTS: list => sortBy(list, 'points').reverse(),
};

const updateSearchTopStoriesState = (hits, page) => (prevState) => {
  const { searchKey, results } = prevState;
  const oldHits = results && results[searchKey]
    ? results[searchKey].hits
    : [];
  const updatedHits = [
    ...oldHits,
    ...hits
];
  return {
    results: {
...results,
      [searchKey]: { hits: updatedHits, page }
    },
    isLoading: false
  };
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      results: null,
      searchKey: '',
      searchTerm: DEFAULT_QUERY,
      gorequest: false,
      error: null,
      isLoading: false,
      sortKey: 'NONE',
      isSortReverse: false,
    };
    this.needsToSearchTopStories = this.needsToSearchTopStories.bind(this);
    this.setSearchTopStories = this.setSearchTopStories.bind(this);
    this.fetchSearchTopStories = this.fetchSearchTopStories.bind(this);
    this.onSearchChange = this.onSearchChange.bind(this);
    this.onSearchSubmit = this.onSearchSubmit.bind(this);
    this.onDismiss = this.onDismiss.bind(this);
    this.onSort = this.onSort.bind(this);
  }

  onSort(sortKey) {
    const isSortReverse = this.state.sortKey === sortKey && !this.state.isSortReverse;
    this.setState({ sortKey, isSortReverse });
  }


  /** fetchSearchTopStories $拉取新闻标题数据方法 
   * ES6原声fetch异步请求数据,此API功能强大。
  */
  fetchSearchTopStories(searchTerm, page = 0) {
    this.setState({ isLoading: true });
    this.setState({ gorequest: true });
    fetch(`${PATH_BASE}${PATH_SEARCH}?${PARAM_SEARCH}${searchTerm}&${PARAM_PAGE}${page}&${PARAM_HPP}${DEFAULT_HPP}`)
      .then(response => response.json())
      .then(result => this.setSearchTopStories(result))
      .catch(e => this.setState({ error: e }));
  }
  /** fetchSearchTopStories $是否需要拉数据判断 
   * 
  */
  needsToSearchTopStories(searchTerm) {
    return !this.state.results[searchTerm];
  }
  /** setSearchTopStories $拉取更多新闻标题数据方法 
   * ES6...拓展运算符号大多数情况下用来组合多个对象或者数组。
  */
  setSearchTopStories(result) {
    const { hits, page } = result;
    // const { searchKey, results } = this.state;
    // const oldHits = results && results[searchKey] ? results[searchKey].hits : [];
    // const updatedHits = [
    //   ...oldHits,
    //   ...hits
    // ];
    // this.setState({
    //   results: {
    //     ...results,
    //     [searchKey]: { hits: updatedHits, page }
    //   },
    //   gorequest: false,
    //   isLoading: false,
    // });
    this.setState(updateSearchTopStoriesState(hits, page));
    // this.setState(prevState => {
    //       const { searchKey, results } = prevState;
    //       const oldHits = results && results[searchKey]
    //                       ? results[searchKey].hits
    //                       : [];
    //       const updatedHits = [
    //                 ...oldHits,
    //                 ...hits
    //             ];
    //             return {
    //               results: {
    //                 ...results,
    //                 [searchKey]: { hits: updatedHits, page }
    //               },
    //               isLoading: false
    //             };
    //   });
  }

  /** onDismiss $删除方法
   * 利用filter过滤数据并返回新数据                 
   * ES6...拓展运算符。
  */
  onDismiss(id) {
    this.setState((prevState) => {
    const { searchKey, results } = prevState;
    const { hits, page } = results[searchKey];

    const isNotId = items => items.objectID !== id;
    const updatedHits = hits.filter(isNotId);
      return {
        results: {
          ...results,
          [searchKey]: { hits: updatedHits, page }
        }
      }
    })
    // const { searchKey, results } = this.state;
    // const { hits, page } = results[searchKey];

    // const isNotId = items => items.objectID !== id;
    // const updatedHits = hits.filter(isNotId);

    // this.setState({
    //   results: {
    //     ...results,
    //     [searchKey]: { hits: updatedHits, page }
    //   }
    // })

  }
  /** onSearchChange 
   *                
  */
  onSearchChange(event) {
    this.setState({ searchTerm: event.target.value });
  }

  onSearchSubmit(event) {
    event.preventDefault();
    this.setState(( prevState ) => {
      const { searchTerm } = prevState;
      if (this.needsToSearchTopStories(searchTerm)) {
        this.fetchSearchTopStories(searchTerm);
      }
      return { searchKey: searchTerm } 
    });
  }

  componentDidMount() {
    const { searchTerm } = this.state;
    this.setState({ searchKey: searchTerm });
    this.fetchSearchTopStories(searchTerm);
  }

  render() {

    const {
      searchTerm,
      results,
      searchKey,
      gorequest,
      error,
      isLoading,
      sortKey,
      isSortReverse
          } = this.state;

    const page = (
      results &&
      results[searchKey] &&
      results[searchKey].page
    ) || 0;

    const list = (
      results &&
      results[searchKey] &&
      results[searchKey].hits
    ) || [];

    if (error) {
      return <p>Something went wrong.</p>;
    }

    return (
      <div className="page">
        <div className="interactions">
          <Search
            value={searchTerm}
            onChange={this.onSearchChange}
            onSubmit={this.onSearchSubmit}
          >
            Search
          </Search>
          {error
            ? <div className="interactions">
              <p>Something went wrong.</p>
            </div>
            : results && <Table
              sortKey={sortKey}
              list={list}
              isSortReverse={isSortReverse}
              onSort={this.onSort}
              onDismiss={this.onDismiss}
            />
          }
          <div className="interactions">
            <ButtonWithLoading
              isLoading={isLoading}
              onClick={() => this.fetchSearchTopStories(searchKey, page + 1)}>
              More
            </ButtonWithLoading>
          </div>
        </div>
      </div>
    )


  }
}

class Search extends Component {

  componentDidMount() {
    if (this.input) {
      if (!this.props.value) {
        this.input.focus();
      }
    }
  }

  render() {
    const { value, onChange, onSubmit, children } = this.props;
    return (
      <form onSubmit={onSubmit}>
        {children}{' '}
        <input
          type='text'
          value={value}
          onChange={onChange}
          ref={(node) => { this.input = node; }}
        />
        <button type="submit">
          {children}
        </button>
      </form>
    );
  }
}


const largeColumn = {
  width: '50%',
};
const midColumn = {
  width: '10%',
};
const smallColumn = {
  width: '10%',
};
/** isSearched 
 *   一个用来查找过滤高阶函数结合filter使用          
*/
const isSearched = searchTerm => items => items.title.toLowerCase().includes(searchTerm.toLowerCase());

const Table = ({ list, onDismiss, sortKey, onSort, isSortReverse }) => {

const sortedList = SORTS[sortKey](list);
const reverseSortedList = isSortReverse
  ? sortedList.reverse()
  : sortedList;

  return (
    <div className='table'>
      <div className="table-header">
        <span style={{ width: '50%' }}>
          <Sort
            sortKey={'TITLE'}
            onSort={onSort}
            activeSortKey={sortKey}
          > Title
        </Sort>
        </span>
        <span style={{ width: '10%' }}>
          <Sort
            sortKey={'AUTHOR'}
            onSort={onSort}
            activeSortKey={sortKey}
          >Author
        </Sort>
        </span>
        <span style={{ width: '10%' }}>
          <Sort
            sortKey={'COMMENTS'}
            onSort={onSort}
            activeSortKey={sortKey}
          > Comments
        </Sort>
        </span>
        <span style={{ width: '10%' }}>
          <Sort
            sortKey={'POINTS'}
            onSort={onSort}
            activeSortKey={sortKey}
          >
            Points
        </Sort>
        </span>
        <span style={{ width: '10%' }}>
          Archive
      </span>
      </div>
      {reverseSortedList.map(item =>
        <div key={item.objectID} className='table-row'>
          <span style={largeColumn}>
            <a href={item.url}>{item.title}</a>
          </span>
          <span style={smallColumn}>{item.author}</span>
          <span style={smallColumn}>{item.num_comments}</span>
          <span style={smallColumn}>{item.points}</span>
          <span style={smallColumn}>
            <Button
              onClick={() => onDismiss(item.objectID)}
              className='button-inline'
            >
              Dismiss
            </Button>
          </span>
        </div>
      )}
    </div>
  )
}

const Sort = ({ sortKey, onSort, children, activeSortKey,}) => {
  const sortClass = classNames(
    'button-inline',
    { 'button-active': sortKey === activeSortKey }
  )

  return (
    <Button
      onClick={() => onSort(sortKey)}
      className={sortClass}
    >
    {children}
    </Button>
  ) 
}
             
                

const Button = ({ onClick, className, children }) => {
  return (
    <button
      onClick={onClick}
      className={className}
      type="button"
    >
      {children}
    </button>
  )
}

Button.propTypes = {
  onClick: PropTypes.func.isRequired,
  className: PropTypes.string,
  children: PropTypes.node.isRequired,
};

Button.defaultProps = {
  className: 'wooo',
};

const Gorequest = ({ className, children }) => {
  return (
    <div>
      <br />
      <FontAwesomeIcon icon="spinner" pulse size="lg" />
    </div>
  )
}

const Loading = () => <div><br /><FontAwesomeIcon icon="spinner" pulse size="lg" /></div>

const withLoading = (Component) => ({ isLoading, ...rest }) =>
  isLoading
    ? <Loading />
    : <Component { ...rest } />

const ButtonWithLoading = withLoading(Button);

export default App;
export {
  Button,
  Search,
  Table,
};


